html {
 background: url("/assets/planets.png") no-repeat center center fixed;
  /* new background inserted above here */
  box-sizing: border-box;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  z-index:0;
  overflow: hidden;
}

h1 {
  text-align: center;
  font-size: 50px;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  color: greenyellow;
  /* changed font from white to greenyellow*/
  -webkit-text-stroke: 2px black;
}

h2 {
  text-align: center;
  font-size: 30px;
  color: greenyellow;
  /* changed font from white to greenyellow*/
  -webkit-text-stroke: 1px black;
}

#start {
    text-align: center;
    font-size: 25px;
    background-color:purple;
    
}

#timer{
    color: red;
    /* changed color from white to red*/
}

.grid {
  width: 750px;
  height: 400px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  cursor: url('/assets/mallet.png'), pointer;
}

.hole {
  flex: 1 0 33.33%;
  overflow: hidden;
  position: relative;
}

.hole:after {
  display: block;
  /*replaced the holes with ufos*/
  background: url("https://www.freepnglogos.com/images/ufo-26782.html") bottom center no-repeat;
  background: url("/assets/ufo_02.png") bottom center no-repeat;
  background-size:contain;
  content:'';
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: -40%;
  z-index: 2;
  pointer-events: none;
}

.mole {
  /* changed moles to aliens */
 background:url('https://www.clipartmax.com/png/full/21-214073_alien-clipart-free.png') bottom center no-repeat;
  background-size: 40%;
  position: absolute;
  top: 100%;
  width: 100%;
  height: 100%;
  transition: transform 0.5s;
  transition: all 1s;
  pointer-events:auto;
}

.hole.show .mole {
  top: 0;
  transform: scale(1.1);
}
